.contractor_order {
	margin-top: 45px;
	background-color: #fff;
	border-radius: 15px;
	width: 100%;
	padding: 25px;
	color: #0056d3;
	font-weight: 300;
	font-size: 20px;
	line-height: 23px;
}

.contractor_order_title {
	font-weight: 600;
}

.contractor_order_content {
	display: flex;
	justify-content: space-between;
	margin-top: 25px;
}

.contractor_order_text {
	flex-basis: 50%;
}

.contractor_order_ch {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.contractor_ch {
	display: flex;
	justify-content: flex-end;
	align-content: space-between;
	align-items: center;
}

.contractor_order_footer {
	display: flex;
	justify-content: center;
}

.checkbox {
	border: 1px solid #0056d3;
	border-radius: 25%;
	height: 25px;
	width: 25px;
	min-height: 25px;
	min-width: 25px;
	margin-right: 20px;
	font-size: 1.5rem;
	text-align: center;
	padding: 2px;
	background: var(--checkboxBC);
}

.label {
	margin-right: 15px;
	font-weight: 600;
	font-size: 15px;
	line-height: 18px;
}

.contractor_order_footer {
	margin-top: 25px;
}

.contractor_order_title p {
	margin-bottom: 15px;
}

.blue_btn {
	margin-right: 25px;
	background: #26a7f3;
	color: #fff;
}

.white_btn {
	margin-left: 25px;
	color: #26a7f3;
	background: #fff;
}

.blue_btn:hover {
	background: #007acc;
}

.white_btn:hover {
	background: rgb(236, 236, 236);
}

.blue_btn,
.white_btn {
	font-weight: 600;
	font-size: 20px;
	line-height: 23px;
	box-shadow: 10px 10px 25px rgba(3, 148, 141, 0.25);
	border-radius: 15px;
	width: 235px;
	padding-top: 15px;
	padding-bottom: 15px;
}
