.form_box_checkbox {
	font-weight: 300;
	font-size: 16px;
	line-height: 19px;
	display: flex;
	align-items: center;
	margin-bottom: 2.5vh;
	cursor: pointer;
}

.form_box_checkbox .checkbox {
	border: 1px solid #0056d3;
	border-radius: 25%;
	height: 25px;
	width: 25px;
	min-height: 25px;
	min-width: 25px;
	margin-right: 20px;
	font-size: 1.5rem;
	text-align: center;
	padding: 2px;
}
