.filter_small_container {
	position: relative;
	cursor: pointer;
}

.filter_small_container img {
	padding-left: 60px;
	padding-right: 25px;
}

.filter_small_container:hover .filter_img_small {
	display: block;
}

.filter_img_small {
	top: -10px;
	left: 50px;
	display: none;
	position: absolute;
	background-color: #fff;
	width: max-content;
	padding: 10px 35px 35px 10px;
	box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.25);
	border-radius: 15px;
}
.filter_img_small img {
	padding-left: 0;
	padding-right: 0;
	margin-right: 25px;
}

.filter_small_title {
	display: flex;
	font-weight: 300;
	font-size: 20px;
	line-height: 23px;
	align-items: center;
}

.filter_small_content {
	display: flex;
	justify-content: space-between;
}

.filter_small_content p {
	font-weight: 600;
	font-size: 20px;
	line-height: 23px;
	padding-top: 20px;
	padding-bottom: 30px;
}

.small_content_col1,
.small_content_col2 {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-left: 6.5vw;
}

.small_content_col1 div {
	margin-bottom: 15px;
}

.small_content_col1 div:hover {
	color: #26a7f3;
}

.small_col2 {
	display: flex;
	align-items: center;
}

.small_col2 div {
	padding: 10px;
	white-space: nowrap;
}

.small_col2_day {
	border: 1px solid rgba(0, 86, 211, 0.25);
	box-sizing: border-box;
	border-radius: 5px;
}
