.authorization_form {
	padding: 55px;
	background-color: #ffffff;
	box-shadow: 5px 5px 50px rgba(0, 0, 0, 0.15);
	border-radius: 15px;
	
}

.form_box {
	display: flex;
	color: #0056d3;
	flex-direction: column;
	align-items: center;
}

.form_box_title {
	font-size: 25px;
	line-height: 29px;
}

.form_box_inputs {
	display: flex;
	flex-direction: column;
	padding-top: 3vh;
	margin-bottom: 15px;
	width: 20vw;
}

.form_box_inputs button {
	color: #ffffff;
	border-radius: 15px;
	box-shadow: 10px 10px 25px rgba(3, 148, 141, 0.25);
	font-weight: 500;
	font-size: 20px;
	line-height: 23px;
	padding: 18px;
	width: 15vw;
	margin: 0 auto;
}

.form_box_inputs button:hover {
	background-color: #052572;
}

.form_box_inputs input {
	border: 1px solid #0056d3;
	border-radius: 15px;
	padding: 19px;
	margin-bottom: 2vh;
	color: #0056d3;
	font-weight: 300;
	font-size: 18px;
	line-height: 21px;
}

.form_box_inputs input::placeholder {
	color: var(--placeHolderColor);
	font-weight: 300;
	font-size: 18px;
	line-height: 21px;
}
