.profile_container {
	padding-top: 15vh;
}
.profile {
	box-shadow: 5px 5px 50px rgba(0, 0, 0, 0.15);
	border-radius: 15px;
	padding: 45px 100px;
	font-weight: 300;
	font-size: 20px;
	line-height: 33px;
}

.profile_header,
.profile_data {
	display: flex;
}

.profile_header {
	justify-content: space-between;
}

.profile_header button {
	padding: 25px 35px;
	background-color: #0056d3;
	color: #fff;
	border-radius: 15px;
	font-weight: normal;
	font-size: 20px;
	line-height: 23px;
}

.profile_avatar {
	border-radius: 50%;
	background-color: #0056d3;
	overflow: hidden;
	margin-right: 55px;
}

.profile_avatar img {
	width: 150px;
	height: 150px;
}

.profile_menu {
	margin-top: 25px;
	width: max-content;
	font-weight: 600;
}

.profile_menu div {
	cursor: pointer;
}

.profile_menu div:hover {
	color: #26a7f3;
}

.order_list {
	padding-left: 30px;
}

.order_list::before {
	content: "\2022";
	font-size: 18px;
	margin-right: 10px;
}
