.rols_container {
	margin-bottom: 2vh;
	height: calc(19px + 19px + 18px);
	font-weight: 300;
	font-size: 18px;
	line-height: 21px;
	position: relative;
}

.rols {
	overflow: hidden;
	height: calc(19px + 19px + 22px);
	border: 1px solid #0056d3;
	border-radius: 15px;
	background-color: #ffffff;
}

/*
.rols:hover {
	height: auto;
}
*/

.rols_list div {
	padding: 19px;
	cursor: pointer;
}


.rols_list {
	position: relative;
}

.rols_list img {
	position: absolute;
	right: 1.2rem;
	top: 1.5rem;
	transition: all 0.2s linear;
}

.rols_list:hover img {
	transform: rotate(90deg);
}

.rols_list div:hover {
	background-color: #0056d3;
	color: #ffffff;
}