.search_orders {
	padding-bottom: 100px;
	color: #0056d3;
}

.search_order {
	margin-top: 30px;
	width: 100%;
	background-color: #fff;
	border-radius: 15px;
	padding: 15px 25px;
	display: grid;
	grid-template-columns: repeat(3, calc(33.3% - 10px));
	grid-gap: 15px;
}

.search_order span div {
	position: relative;
}

.hot_img::after {
	content: "Срочный проект";
	position: absolute;
	left: 50px;
	top: -10px;
	white-space: nowrap;
	font-weight: 300;
	font-size: 20px;
	line-height: 23px;
	background: rgba(255, 255, 255, 0.45);
	color: #fff;
	border-radius: 10px;
	padding: 12px 35px;
	display: none;
}

.hot_img:hover::after {
	display: block;
}

.search_order span {
	font-weight: 600;
	font-size: 20px;
	line-height: 23px;
	display: flex;
	justify-content: space-between;
}

.smallDescription {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.orderDetails {
	display: flex;
	align-items: flex-end;
}

.search_orders_present {
	height: calc(100vh - 30vh - 24px);
	margin: 0 auto;
}
