.search {
	font-weight: 300;
	font-size: 20px;
	line-height: 23px;
	color: #0056d3;
	width: 100vw;
}

.search_header {
	/*position: fixed;*/
	background-color: #fff;
	padding-top: calc(6vh + 50px);
}

.search_header_filter {
	padding: 25px;
	display: flex;
	align-items: center;
	flex-direction: row;
	flex-wrap: wrap;
}

.search_filter_choice,
.search_filter_text,
.filter_small_container {
	margin-bottom: 10px;
}

.search_filter_text {
	cursor: default;
}

.search_filter_choice {
	margin-right: 25px;
	display: flex;
	background-color: #0056d3;
	border-radius: 15px;
	font-weight: 500;
	font-size: 20px;
	line-height: 23px;
	padding: 13px;
	color: #fff;
}

.search_filter_choice img {
	margin-left: 15px;
	margin-right: 0;
	cursor: pointer;
}
