/*Header*/

header {
	position: fixed;
	width: 100vw;
	background-color: #ffffff;
	box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
	z-index: 1;
}

.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header_logo img {
	height: 6vh;
	margin: 12px 0;
}

.header_btn button {
	font-weight: 600;
	font-size: 20px;
	line-height: 23px;
	padding: 10px 70px;
	border-radius: 15px;
	border: 2px solid #0056d3;
	background: none;
}

.header_btn button:hover {
	background-color: #26a7f3;
	color: #ffffff;
}

.menu_img {
	max-height: 30px;
	cursor: pointer;
}
