.engineer_money {
	height: 100vh;
	width: 100vw;
	z-index: 4;
	position: absolute;
	backdrop-filter: blur(50px);
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: 300;
	font-size: 20px;
	line-height: 23px;
	color: #0056d3;
}

.engineer_money_container {
	background-color: #fff;
	padding: 25px 25px 55px 25px;
	width: 30vw;
	min-width: 500px;
	border-radius: 15px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.money_container_h2 {
	background: #fff;
	padding: 25px;
	border-radius: 15px;
	position: relative;
}

.money_container_h2 img {
	position: absolute;
	top: -20px;
	right: -20px;
}

.engineer_money_exit {
	width: 100%;
	display: flex;
	justify-content: flex-end;
}

.engineer_money_title {
	font-weight: 600;
}

.engineer_money_sum {
	padding-top: 20px;
}

.engineer_money_container input {
	border: 1px solid #0056d3;
	border-radius: 15px;
	color: #0056d3;
	padding: 15px;
	margin-top: 35px;
}

.card_number,
.all-in {
	width: 65%;
}

.all-in input {
	width: 100%;
}

.engineer_money_container input::placeholder {
	color: #0056d3;
}

.engineer_money_container button {
	background: #26a7f3;
	box-shadow: 10px 10px 25px rgba(3, 148, 141, 0.25);
	border-radius: 15px;
	color: #fff;
	padding: 16px 15%;
}

.all-in div {
	font-weight: 200;
	font-size: 15px;
	line-height: 18px;
	margin: 15px auto 35px 15px;
	cursor: pointer;
}

.error {
	color: red;
}
