.search_input {
	position: relative;
}

.search_input img {
	position: absolute;
	top: 10px;
	right: 20px;
	cursor: pointer;
}

.search_header input {
	width: 100%;
	border: 2px solid #0056d3;
	border-radius: 15px;
	padding: 15px;
	color: #0056d3;
	font-weight: 300;
	font-size: 18px;
	line-height: 21px;
}

.search_header input::placeholder {
	color: #0056d3;
}
