.menu {
	z-index: 2;
	padding-top: 25px;
	padding-bottom: 25px;
	background-color: #fff;
	position: absolute;
	min-height: 100vh;
	width: 100vw;
	top: 0;
	left: 0;
	color: #0056d3;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.menu_header {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}

.menu_content {
}

.menu_content_title {
	width: 100%;
	text-align: center;
	font-weight: 600;
	font-size: 46px;
	line-height: 54px;
	margin-bottom: 15vh;
}

.menu_content_menu {
	display: flex;
	justify-content: space-between;
	width: 80%;
}

.content_menu_list {
	font-weight: 600;
	font-size: 25px;
	line-height: 40px;
}

.content_menu_list div {
	cursor: pointer;
}

.content_menu_list div:hover {
	color: #26a7f3;
}

.content_menu_data {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.content_menu_avatar {
	border-radius: 50%;
	background-color: #0056d3;
	overflow: hidden;
}

.content_menu_avatar img {
	width: 150px;
	height: 150px;
	margin-bottom: 10px;
}

.menu_data_name {
	margin-top: 10px;
	font-weight: 500;
	font-size: 25px;
	line-height: 45px;
}

.menu_data_reputation {
	display: flex;
	justify-content: space-between;
	width: calc(15px * 5 + 20px);
}
.menu_data_reputation img {
	height: 15px;
}

.menu_data_money,
.menu_data_id {

	font-size: 20px;
	line-height: 23px;
	color: #0056d3;
}

.menu_footer {

	font-size: 16px;
	line-height: 19px;
	text-align: center;
}
